"use client";
import Link from "next/link";
import { useState } from "react";
import ThemeToggle from "../shared/ThemeToggle";
import { FaPalette, FaBrush, FaTimes } from "react-icons/fa"; // Art-inspired icons

export default function WebsiteHeader() {
  // Track whether the mobile menu is open
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  // Toggle menu visibility
  const toggleMenu = () => setIsMenuOpen((prev) => !prev);

  return (
    <header className="sticky top-0 z-50 bg-[rgba(244,244,244,0.5)] text-black shadow-sm transition-colors duration-300 text-sm">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex items-center justify-between h-16">
          {/* Logo */}
          <div className="flex-shrink-0">
            <Link href="/" className="flex items-center space-x-2">
              <FaPalette className="text-lg lg:text-xl" />
              <span className="text-lg lg:text-xl font-bold tracking-wide">
                Hazel Thrills
              </span>
            </Link>
          </div>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex gap-6 font-medium">
            {["gallery", "services", "about", "contact", "blog", "shop"].map(
              (path) => (
                <Link
                  key={path}
                  href={`/${path}`}
                  className="hover:text-gray-500 transition-colors duration-200 capitalize text-xs lg:text-sm"
                >
                  {path}
                </Link>
              )
            )}
          </nav>

          {/* Right-side actions */}
          <div className="flex items-center space-x-4">
            {/* Theme toggle with art-inspired icon (FaBrush) */}
            <ThemeToggle icon={<FaBrush />} />

            {/* Login button for desktop */}
            <Link
              href="/login"
              className="hidden md:inline-block border border-black  px-6 py-1.5 rounded hover:bg-black hover:text-white  transition-colors duration-200"
            >
              Login
            </Link>

            {/* Mobile menu toggle */}
            <button
              className="md:hidden p-2"
              onClick={toggleMenu}
              aria-label="Toggle Menu"
            >
              {isMenuOpen ? (
                <FaTimes className="text-xl" />
              ) : (
                <FaPalette className="text-xl" />
              )}
            </button>
          </div>
        </div>

        {/* Mobile Menu */}
        <div
          className={`lg:hidden overflow-hidden transition-all duration-300 ease-in-out ${
            isMenuOpen ? "max-h-[500px] opacity-100" : "max-h-0 opacity-0"
          }`}
        >
          <nav className="flex flex-col gap-4 py-4 font-medium">
            {["gallery", "services", "about", "contact", "blog", "shop"].map(
              (path) => (
                <Link
                  key={path}
                  href={`/${path}`}
                  onClick={() => setIsMenuOpen(false)} // Auto-close on link click
                  className="hover:text-gray-500 transition-colors duration-200 capitalize"
                >
                  {path}
                </Link>
              )
            )}

            {/* Login link (mobile version) */}
            <Link
              href="/login"
              onClick={() => setIsMenuOpen(false)}
              className="mt-2 border border-black px-4 py-2 rounded text-center hover:bg-black hover:text-white  transition-colors duration-200"
            >
              Login
            </Link>
          </nav>
        </div>
      </div>
    </header>
  );
}
